<!--dorf：Details of refund charge的缩写-->
<template>
  <div>
    <Form label-position="right" :label-width="90" inline style="background:#fff;padding-top: 24px" class="ivu-common-shadow">
      <FormItem label="城市" required>
        <Select
          v-model="formData.cityCode"
          class="e-input"
          filterable>
          <Option v-for="(option,index) in cityList" :value="option.id" :key="index">{{option.name}}</Option>
        </Select>
      </FormItem>
      <FormItem label="缴费单号范围">
        <Input class="e-input" v-model="formData.startSequence"/> &nbsp;&nbsp;至&nbsp;&nbsp; <Input class="e-input" v-model="formData.endSequence"/>
      </FormItem>
      <FormItem label="操作人">
        <Input class="e-input" v-model="formData.createName"/>
      </FormItem>
      <FormItem label="操作时间">
        <DatePicker class="e-input" style="width: 160px" :editable="false" v-model="formData.startDateVal" @on-change="setStartTime" type="date" format="yyyy-MM-dd"></DatePicker> &nbsp;&nbsp;至&nbsp;&nbsp;
        <DatePicker class="e-input" style="width: 160px" :editable="false" v-model="formData.endDateVal" @on-change="setEndTime" type="date" format="yyyy-MM-dd"></DatePicker>
      </FormItem>
      <FormItem style="float: right">
        <Button type="primary" style="margin-right: 10px;" @click="searchTableData" :loading="tableLoading">查询</Button>
        <Button style="margin-right: 10px;" @click="resetFields">重置</Button>
      </FormItem>
    </Form>
    <Table style="width: 100%;margin-top: 10px" :columns="cols" :data="tableData" :loading="tableLoading">
      <template slot-scope="{ row }"  slot="operation">
        <!--financeType：0，收入、1，支出，只有在支出的时候才有电子凭证-->
        <a v-if="row.financeType == 1" @click='sendVoucher(row)'>发送电子凭证</a>
      </template>
    </Table>
    <div style="margin: 10px;" :class="{ 'page-fix': tableData.length < 3 }">
      <div style="text-align: right;">
        <Page
          :total="formData.totalCount"
          :page-size="formData.pageSize"
          :current.sync="formData.pageNo"
          show-elevator
          show-sizer
          show-total
          @on-page-size-change="pageSizeChange"
          @on-change="changePage"></Page>
      </div>
    </div>
    <Modal :mask-closable="false"
           title="发送缴费电子凭证"
           v-model="voucherShow"
           width="960"
           :footer-hide="true">
      <Voucher
        v-if="voucherShow"
        @success="voucherShow = false"
        :cityCode="formData.cityCode"
        :row="row">
      </Voucher>
    </Modal>
  </div>
</template>

<script>
import { getCityList, getCurrentTime } from '@/api/bd'
import { getStuCardPayBills } from '@/api/finance'
import { dateFormat } from '@/utils/utils'
import Voucher from './voucher'
export default {
  components: {
    Voucher
  },
  data () {
    return {
      row: {},
      tableLoading: false,
      formData: {
        cityCode: '',
        startSequence: '',
        endSequence: '',
        createName: '',
        startDateVal: '',
        startDate: '',
        endDateVal: '',
        endDate: '',
        totalCount: 0,
        pageNo: 1,
        pageSize: 10
      },
      tableData: [],
      cols: [
        { title: '操作', align: 'center', slot: 'operation' },
        { title: '缴费单号', align: 'center', key: 'number' },
        { title: '学员编号', align: 'center', key: 'studentCard' },
        { title: '学员姓名', align: 'center', key: 'studentName' },
        { title: '收入/支出', align: 'center', key: 'financeTypeName' },
        { title: '缴费方式', align: 'center', key: 'chargeTypeName' },
        { title: '收/支金额', align: 'center', key: 'amount' },
        { title: '交易人姓名', align: 'center', key: 'person' },
        { title: '操作人', align: 'center', key: 'createrName' },
        { title: '操作时间', align: 'center', key: 'createDate' },
        { title: '银行名称', align: 'center', key: 'swipeBankName' },
        { title: '银行卡号', align: 'center', key: 'cardNo' },
        { title: '原缴费服务中心', align: 'center', key: 'payServiceCenterName' },
        { title: '款项所属服务中心', align: 'center', key: 'serviceCenterName' },
        { title: '流水号', align: 'center', key: 'certificateNo' },
        { title: '授权码', align: 'center', key: 'referenceNo' },
        { title: '缴费金额', align: 'center', key: 'payprice' },
        { title: '缴费时间', align: 'center', key: 'payDate' },
        { title: '交易参考号', align: 'center', key: 'referenceNo' },
        { title: '交易时间', align: 'center', key: 'createDate' }
      ],
      cityList: [],
      voucherShow: false
    }
  },
  methods: {
    sendVoucher (row) {
      this.row = row
      this.voucherShow = true
    },
    getCityHandler () {
      getCityList().then(data => {
        this.cityList = data
      }).catch(err => {
        this.$Message.error({
          content: '获取城市列表失败：' + err.message
        })
      })
    },
    getCurrentTimeHandler () {
      getCurrentTime().then(data => {
        const startTime = dateFormat(data - 24 * 60 * 60 * 1000, 'yyyy-MM-dd')
        const endTime = dateFormat(data, 'yyyy-MM-dd')
        this.formData.startDate = startTime
        this.formData.startDateVal = startTime
        this.formData.endDate = endTime
        this.formData.endDateVal = endTime
      }).catch(err => {
        this.$Message.error({
          content: '获取服务器当前时间失败：' + err.message,
          duration: 5
        })
      })
    },
    /**
       * 电子收退费明细"查询"
       */
    searchTableData () {
      this.formData.pageNo = 1
      this.getTableData()
    },
    /**
       * 获取收退费明细
       */
    getTableData () {
      if (!this.formData.cityCode) {
        this.$Message.warning({
          content: '请选择城市',
          duration: 3
        })
        return
      }
      if (!this.formData.startDateVal) {
        this.$Message.warning({
          content: '操作开始时间不能为空',
          duration: 3
        })
        return
      }
      if (!this.formData.endDateVal) {
        this.$Message.warning({
          content: '操作结束时间不能为空',
          duration: 3
        })
        return
      }
      if (this.formData.startDateVal && this.formData.endDateVal && this.formData.startDateVal > this.formData.endDateVal) {
        this.$Message.warning({
          content: '开始时间不能晚于结束时间',
          duration: 3
        })
        return
      }
      this.tableLoading = true
      getStuCardPayBills(this.formData).then(data => {
        this.formData.totalCount = data.totalCount
        this.tableData = data.records
      }).catch(err => {
        this.tableData = []
        this.formData.totalCount = 0
        this.$Message.error({
          content: '获取电子钱包收退费列表失败：' + err.message
        })
      }).finally(() => {
        this.tableLoading = false
      })
    },
    /**
       * 收退费明细翻页
       * @param index
       */
    changePage (index) {
      this.formData.pageNo = index
      this.getTableData()
    },
    /**
       * 收退费明细变更pageSize
       * @param size
       */
    pageSizeChange (size) {
      this.formData.pageNo = 1
      this.formData.pageSize = size
      this.getTableData()
    },
    /**
       * 计算两个时间之间的天数
       * @param date1 小日期
       * @param date2 大日期
       * @returns {number}
       * @constructor
       */
    DateMinus (date1, date2) {
      const start = new Date(date1)
      const end = new Date(date2)
      const days = end.getTime() - start.getTime()
      const day = parseInt(days / (1000 * 60 * 60 * 24))
      return day
    },
    validateDate (which) {
      if (this.formData.startDate && this.formData.endDate) {
        if (this.DateMinus(this.formData.startDate, this.formData.endDate) > 30) {
          this.$Message.warning({ content: '时间跨度不能大于30天，请重新选择', duration: 5 })
          if (which === 0) {
            this.formData.startDateVal = ''
            this.formData.startDate = ''
          } else if (which === 1) {
            this.formData.endDateVal = ''
            this.formData.endDate = ''
          } else {

          }
        }
      }
    },
    setStartTime (val) {
      this.formData.startDate = val
      this.validateDate(0)
    },
    setEndTime (val) {
      this.formData.endDate = val
      this.validateDate(1)
    },
    /**
       * 重置收退费明细搜索条件
       */
    resetFields () {
      this.formData = {
        cityCode: '',
        startSequence: '',
        endSequence: '',
        createName: '',
        startDateVal: '',
        startDate: '',
        endDateVal: '',
        endDate: '',
        totalCount: 0,
        pageNo: 1,
        pageSize: 10
      }
    }
  },
  watch: {
    cityList (val) {
      if (val.length === 1) {
        this.formData.cityCode = val[0].id
      }
    }
  },
  mounted () {
    this.getCityHandler()
    this.getCurrentTimeHandler()
  }
}
</script>

<style scoped lang="less">
  .e-input {
    width: 130px;
  }
</style>
