/* eslint-disable */
import axios from '@/libs/api.request'
// 退费班级列表查询
export function queryRefundList (params) {
    return axios.request({
        url: '/register/queryRefundList',
        method: 'post',
        data: params
    })
}
// 查询书籍费退费列表
export function queryRefundList2 (params) {
    return axios.request({
        url: '/fee/queryRefundList',
        method: 'post',
        data: params
    })
}
/**
 * 发送退费电子凭证(班级退费凭证)
 * @param params
 * @returns {*}
 */
export const sendRefundVoucher = params => {
    return axios.request({
        url: '/message/sendRefundVoucher',
        params: params,
        method: 'get'
    })
}
// 书籍退费凭证
export const sendRefundVoucher2 = params => {
    return axios.request({
        url: '/fee/queryRefundDetailById',
        params: params,
        method: 'get'
    })
}