<template>
  <section>
    <Form :model="formData" :rules="formValidate" ref="form" inline :label-width="130">
      <Row type="flex" justify="start">
        <FormItem label="电子凭证接收邮箱" prop="email">
          <Input type="email" clearable placeholder="请输入" style="width: 200px;margin-right: 10px" v-model="formData.email" />
          <Button type="primary" :loading="loading" @click="send" style="margin-right: 10px">发送</Button>
          <Button type="primary" :loading="loading" @click="print">打印</Button>
        </FormItem>
      </Row>
    </Form>
    <Table stripe :columns="tableColumns" :data="tableData"></Table>
  </section>
</template>

<script>
import {
  findMessageSendRecords,
  sendCashOutVoucher
} from '@/api/message'
import {
  getUserExtInfoByType
} from '@/api/bd'
export default {
  props: {
    from: { // ChangeCourse：调课班级，ReadingClass：在读班级
      type: String,
      default: ''
    },
    row: {
      type: Object,
      required: true
    },
    cityCode: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      loading: false,
      formData: {
        email: ''
      },
      formValidate: {
        email: [
          { required: true, message: '邮箱不能为空', trigger: 'blur', type: 'string' },
          { type: 'email', message: 'email格式不正确', trigger: 'blur' }

        ]
      },
      tableColumns: [
        {
          title: '发送时间',
          key: 'createTime'
        },
        {
          title: '接收邮箱',
          key: 'businessNum'
        },
        {
          title: '操作人',
          key: 'creatorName'
        }
      ],
      tableData: []
    }
  },
  mounted () {
    getUserExtInfoByType({
      cityCode: this.cityCode,
      uid: this.row.studentUid,
      type: 'accessEmail'
    }).then(data => {
      this.formData.email = data
    }).catch(err => {
      this.$Message.error({
        content: '查询学员电子凭证邮箱失败：' + err.message,
        duration: 5
      })
    })
    findMessageSendRecords({
      cityCode: this.cityCode,
      businessId: this.row.financeLogId,
      type: 6
      // stageId: '7af9e3b998a94c76a98ed63a53de9a4b'
    }).then(data => {
      this.tableData = data
    }).catch(err => {
      this.$Message.error({
        content: '查询发送记录失败：' + err.message,
        duration: 5
      })
    })
  },
  methods: {
    send () {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.sendOrPrint(0)
        }
      })
    },
    print () {
      this.sendOrPrint(1)
    },
    sendOrPrint (isPrint) {
      sendCashOutVoucher(Object.assign({}, this.row, {
        cityCode: this.cityCode,
        'X-Ca-referer': this.from,
        email: isPrint ? 'print@tal.com' : this.formData.email,
        isPrint
      })).then(data => {
        if (isPrint) {
          this.printData(data)
        } else {
          this.$Message.success('发送成功')
          this.$emit('success')
        }
      }).catch((err) => {
        this.$emit('fail')
        this.$Message.warning({ content: err.message, duration: 5 })
      })
    },
    printData (data) {
      var wind = window.open('', 'newwindow', 'height=700, width=1000, top=100, left=100, toolbar=no, menubar=no, scrollbars=no, resizable=no,location=n o, status=no')
      wind.document.body.innerHTML = data
      wind.print()
    }
  }
}
</script>

