<template>
  <Tabs type="card" style="padding: 10px;width: 100%;min-height: 100%;overflow-y: auto" class="gary">
    <TabPane label="电子钱包管理">
      <TalStuSearch
        ref="stuSearch"
        :show-detail="false"
        :stu-card="queryData.card"
        :stu-city-code="queryData.cityCode"
        @stuInfo="setStuInfo"></TalStuSearch>
      <!--<TalSearchApplyBase :show-detail="false" @stuInfo="setStuInfo"></TalSearchApplyBase>-->
      <Table style="width: 100%;margin-top: 10px" :columns="cols" :data="tableData" :loading="tableLoading">
        <template slot-scope="{ row }"  slot="operation">
          <a @click="withdrawalModalOpen" style="margin-right: 5px">提现</a>
          <a @click='paymentDetails' style="margin-right: 5px">收支明细</a>
          <a @click='tradeRecords'>交易记录</a>
        </template>
        <template slot-scope="{ row }"  slot="cardDetails">
          <div v-for="(item,index) in row.cardDetails" :key="index">{{item.chargeType}}：{{item.cashBalance}}</div>
        </template>
      </Table>
    </TabPane>
    <TabPane label="电子钱包收退费">
      <!--dorf：Details of refund charge（电子钱包收退费明细）的缩写-->
      <dorf></dorf>
    </TabPane>
    <Modal
      :mask-closable="false"
      title="充值"
      v-model="rechargeModal"
      width="960px">
      <Row>
        <Col span="6">
          <span class="name">姓名：</span>
          <span>{{this.studentDetail.realName}}</span>
        </Col>
        <Col span="6">
          <span class="name">学员编号：</span>
          <span>{{this.studentDetail.card}}</span>
        </Col>
        <Col span="6">
          <span class="name">性别：</span>
          <span>{{this.studentDetail.sexName}}</span>
          <span></span>
        </Col>
        <Col span="6">
          <span class="name">年级：</span>
          <span>{{this.studentDetail.gradeName}}</span>
        </Col>
      </Row>
      <Row style="margin-top: 10px">
        <Col span="6">
          <span class="name">账户余额：</span>
          <span>832.00</span>
        </Col>
        <Col span="6">
          <span class="name">现金：</span>
          <span>400.00</span>
        </Col>
        <Col span="6">
          <span class="name">刷卡：</span>
          <span>400.00</span>
        </Col>
        <Col span="6">
          <span class="name">支票：</span>
          <span>400.00</span>
        </Col>
      </Row>
      <Divider />
      <Form inline :label-width="120" style="padding: 20px">
        <Row>
          <FormItem label="款项所属服务中心：">
            <span>海淀服务中心</span>
          </FormItem>
        </Row>
        <Row>
          <Col span="12">
            <FormItem label="充值金额：">
              <Input/>
            </FormItem>
          </Col>
          <Col span="12">
            <FormItem label="电子钱包密码：">
              <Input/>
            </FormItem>
          </Col>
        </Row>
        <Row>
          <Col span="12">
            <FormItem label="缴费人：">
              <Input/>
            </FormItem>
          </Col>
          <Col span="12">
            <FormItem label="关系：">
              <Input/>
            </FormItem>
          </Col>
        </Row>
        <Row>
          <FormItem label="付款方式：">
            <RadioGroup v-model="formData.payMethod">
              <Radio label="现金" disabled></Radio>
              <Radio label="联机POS"></Radio>
              <Radio label="脱机POS"></Radio>
            </RadioGroup>
          </FormItem>
        </Row>
      </Form>
      <div slot="footer">
        <Button @click="withdrawal">提现</Button>
      </div>
    </Modal>
    <Modal
      :mask-closable="false"
      title="提现"
      v-model="withdrawalModal"
      :footer-hide="true"
      width="960px">
      <withdrawal v-if="withdrawalModal" :studentDetail="studentDetail" @success="withdrawalModal = false"></withdrawal>
    </Modal>
    <Modal :mask-closable="false"
           :footer-hide="true"
           title="收支明细查询"
           v-model="payDetailModal"
           width="960px">
        <pay-detail v-if="payDetailModal" :studentDetail="studentDetail"></pay-detail>
    </Modal>
    <Modal :mask-closable="false"
           :footer-hide="true"
           title="交易记录查询"
           v-model="tradeDetailModal"
           width="960px">
      <trade-detail v-if="tradeDetailModal" :studentDetail="studentDetail"></trade-detail>
    </Modal>
  </Tabs>
</template>

<script>
import { queryStudentCard } from '@/api/finance'
import { setFrom } from '@/api/bd'
import Dorf from './dorf'
import PayDetail from './payDetail'
import TradeDetail from './tradeDetail'
import Withdrawal from './withdrawal'
import getUserInfoByUidMixin from '@/components/mixin/getUserInfoByUid'
import { getServiceCenterStatus, auditMissingReportTip } from '@/api/reimbursement'
export default {
  name: 'ewallet',
  mixins: [ getUserInfoByUidMixin ],
  components: {
    Dorf,
    PayDetail,
    TradeDetail,
    Withdrawal
  },
  data () {
    return {
      tableLoading: false,
      tableData: [],
      studentDetail: {},
      cols: Object.freeze([
        { title: '操作', align: 'center', slot: 'operation' },
        { title: '学员编号/电子钱包账号', align: 'center', key: 'card' },
        { title: '学员姓名', align: 'center', key: 'realName' },
        { title: '性别', align: 'center', key: 'sexName' },
        { title: '年级', align: 'center', key: 'gradeName' },
        { title: '电子钱包金额', align: 'center', key: 'cardBalance' }
        // { title: '金额构成', align: 'center', slot: 'cardDetails' }
      ]),
      rechargeModal: false,
      withdrawalModal: false,
      formData: {
        payMethod: ''
      },
      payDetailModal: false,
      tradeDetailModal: false

    }
  },
  methods: {
    setStuInfo (stuInfo) {
      // this.tableData = [stuInfo]
      this.studentDetail = stuInfo
      // 没有电子钱包也不显示学员信息
      this.getDetail().then(tableData => {
        this.tableData = tableData
      })
    },
    /**
       * 查询电子钱包详情
       */
    getDetail () {
      this.tableLoading = true
      return queryStudentCard({
        cityCode: this.studentDetail.cityCode,
        studentCardId: this.studentDetail.stuCardId,
        card: this.studentDetail.card,
        studentId: this.studentDetail.studentId
      }).then(data => {
        if (data) {
          this.studentDetail.cardBalance = data.cardBalance
          this.studentDetail.cardDetails = data.details
          return [this.studentDetail]
        } else {
          return []
        }
      }).catch(err => {
        this.$Message.error({
          content: '获取电子钱包明细失败：' + err.message
        })
      }).finally(() => {
        this.tableLoading = false
      })
    },
    async withdrawalModalOpen () {
      try {
        let interval = this.$store.state.app.missReportInterval
        if (interval) {
          clearInterval(interval)
        }
        await auditMissingReportTip({ isAudit: 0 })
        // 校验是否能提现
        getServiceCenterStatus({
          cityCode: this.studentDetail.cityCode
        }).then(data => {
          // 0: 开启报账 1: 关闭报账
          if (data === 0) {
            this.withdrawalModal = true
          } else if (data === 1) {
            this.$Message.error({
              content: '该服务中心已停用，请联系分校财务启用！'
            })
          }
        }).catch(err => {
          this.$Message.error({
            content: err.message
          })
        })
      } catch (e) {
        if (e.code === '40004') {
          this.$Modal.info({
            title: '提示',
            content: e.message,
            okText: '去补报',
            onOk: () => {
              this.$router.push({
                name: 'prescribeddaily'
              })
            }
          })
        } else {
          this.$Message.error({
            content: e.message,
            duration: 5
          })
        }
      }
    },
    withdrawal () {

    },
    paymentDetails () {
      this.payDetailModal = true
    },
    tradeRecords () {
      this.tradeDetailModal = true
    }
  },
  mounted () {
    setFrom({
      apiName: 'eWalletPage'
    })
  }
}
</script>

