<template>
  <div class="wrapper">
    <div style="text-align: right">打印时间：{{ now }}</div>
    <div class="title">考勤信息打印</div>
    <div class="stu-info">
      <div class="info">学员姓名：{{name}}</div>
      <div class="info">学员编号：{{card}}</div>
      <div class="info">性&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;别：{{sex}}</div>
      <div class="info">年&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;级：{{gradeName}}</div>
    </div>
    <table border="1" width="100%" cellspacing="0" cellpadding="0" bgcolor="#ffffff" bordercolordark="#ffffff" bordercolorlight="#000000" style="border:1px solid #000;border-collapse:collapse">
      <tr>
        <td align="center" width="100px" height="40px">日期</td>
        <td align="center">时间</td>
        <td align="center">班级</td>
        <td align="center" width="100px">考勤状态</td>
      </tr>
      <tr v-for="(item, index) in tableData" :key="index">
        <td align="center">{{item.classDate}}</td>
        <td>{{item.classTime}}</td>
        <td>{{item.className}}</td>
        <td align="center">{{item.modifyType === 0 ? '准时上课' : item.modifyType === 1 ? '迟到' : item.modifyType === 2 ? '缺勤' : '无数据'}}</td>
      </tr>
    </table>
  </div>
</template>

<script>
import { getAttendanceByDate } from '@/api/attendance'
import { dateFormat } from '@/utils/utils'
export default {
  data () {
    return {
      name: '',
      card: '',
      sex: '',
      gradeName: '',
      tableData: [],
      now: dateFormat(new Date())
    }
  },
  mounted () {
    document.documentElement.className = 'printPage'
    document.body.className = 'printPage'
    this.name = this.$route.query.name
    this.card = this.$route.query.card
    this.sex = this.$route.query.sex
    this.gradeName = this.$route.query.gradeName
    getAttendanceByDate({
      studentId: this.$route.query.stuId,
      classDate: this.$route.query.searchDate,
      cityCode: this.$route.query.cityCode
    }).then(data => {
      this.tableData = data
      setTimeout(() => {
        window.print()
      }, 500)
    }).catch(err => {
      this.$Message.error({ content: '查询考勤失败：' + err.message, duration: 5 })
    })
  }
}
</script>
<style lang="less">
  .printPage {
    height: initial;
    overflow: auto!important;
  }
</style>
<style lang="less" scoped>
  .wrapper{
    padding-left: 20px;
    padding-right: 20px;
    .title {
      text-align: center;
      font-size: 16px;
      font-weight: bold;
    }
    .stu-info {
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 10px;
      .info {
        width: 50%;
        margin-top: 10px;
      }
    }
  }
</style>
