<template>
    <div>
      <Row>
        <Col span="6">
          <span class="name">姓名：</span>
          <span>{{studentDetail.realName}}</span>
        </Col>
        <Col span="6">
          <span class="name">学员编号：</span>
          <span>{{studentDetail.card}}</span>
        </Col>
        <Col span="6">
          <span class="name">性别：</span>
          <span>{{studentDetail.sexName}}</span>
          <span></span>
        </Col>
        <Col span="6">
          <span class="name">年级：</span>
          <span>{{studentDetail.gradeName}}</span>
        </Col>
      </Row>
      <Row style="margin-top: 10px">
        <Col span="6">
          <span class="name">账户余额：</span>
          <span>{{studentDetail.cardBalance}}</span>
        </Col>
        <Col span="6" v-for="item in studentDetail.cardDetails" :key="item.chargeType">
          <span class="name">{{item.chargeType}}：</span>
          <span>{{item.cashBalance}}</span>
        </Col>
      </Row>
      <Divider />
      <Form :label-width="120" style="padding: 20px">
        <Row>
          <FormItem label="款项所属服务中心：">
            <span>{{deptData.deptName}}</span>
          </FormItem>
        </Row>
        <Row>
          <Col span="9">
            <FormItem label="获取验证码：">
              <Select v-model="phone" placeholder="请选择手机号码" style="margin-right: 10px;width: 190px">
                <Option v-for="item in phoneList" :value="item.num" :key="item.num">
                  <template v-if="item.num === '0'">
                    注册手机号
                  </template>
                  <template v-else>
                    第{{ item.num }}联系人
                  </template> {{ item.phone }}
                </Option>
              </Select>
            </FormItem>
          </Col>
          <Col span="9">
            <Button type="default" @click="sendMessageHandler" v-if="!sendBtn" class="wd80">发送</Button>
            <Button type="default" :disabled="true" v-else class="wd80">{{msgNum}}秒</Button>
          </Col>
        </Row>
        <Row>
          <Col span="10">
            <FormItem label="验证码：" required >
              <Input v-model="smsCode" style="width: 190px"/>
            </FormItem>
          </Col>
        </Row>
      </Form>
      <div slot="footer" style="text-align: center">
        <Button @click="withdrawal" type="primary">提现</Button>
      </div>
    </div>
</template>

<script>
import {
  queryPhones,
  sendMessage
} from '@/api/sms'
import {
  queryServiceCenterByEmpNo
} from '@/api/clazz'
import { generateCashOutWakeUp } from '@/api/finance'
export default {
  data () {
    return {
      phoneList: [],
      deptData: {},
      phone: '1',
      sendBtn: false,
      msgNum: 30,
      smsCode: ''
    }
  },
  props: {
    studentDetail: {
      type: Object,
      require: true
    }
  },
  methods: {
    withdrawal () {
      if (!this.smsCode) {
        this.$Message.error({
          content: '请输入验证码',
          duration: 5
        })
        return
      }
      // this.$emit('success')
      generateCashOutWakeUp({
        cityCode: this.studentDetail.cityCode,
        studentId: this.studentDetail.studentId,
        uid: this.studentDetail.uid,
        studentCardId: this.studentDetail.stuCardId,
        smsCode: this.smsCode
      }).then(data => {
        // window.open(data.url)
        this.$emit('success')
        this.$router.push({ name: `balanceWithdrawalForMulti`,
          params: { id: new Date().getTime() },
          query: {
            studentName: this.studentDetail.username,
            gradeName: this.studentDetail.gradeName,
            studentId: this.studentDetail.studentId,
            cityCode: this.studentDetail.cityCode,
            studentUid: this.studentDetail.uid
          }
        })
      }).catch(err => {
        this.$Message.error({
          content: err.message,
          duration: 5
        })
      })
    },
    sendMessageHandler () {
      if (!this.sendBtn) {
        if (!this.phone) {
          this.$Message.warning({
            content: '请选择手机号码',
            duration: 3
          })
          return
        }
        this.sendBtn = true
        this.msgNum = 30
        let set
        let req = {
          num: this.phone,
          uid: this.studentDetail.uid,
          cityCode: this.studentDetail.cityCode,
          validType: 'cashOut'
        }
        sendMessage(req).then(res => {
          this.$Message.success({ content: '发送成功', duration: 3 })
          set = setInterval(() => {
            if (this.msgNum <= 0) {
              clearInterval(set)
              this.sendBtn = false
            } else {
              this.msgNum -= 1
            }
          }, 1000)
        }).catch(err => {
          clearInterval(set)
          this.sendBtn = false
          this.$Message.error({
            content: err.message,
            duration: 5
          })
        })
      }
    }
  },
  mounted () {
    let req = {
      cityCode: this.studentDetail.cityCode,
      uid: this.studentDetail.uid
    }
    queryPhones(req).then(res => {
      if (res) {
        this.phoneList = res.map(r => {
          r.num = String(r.num)
          return r
        })
      }
    }).catch(err => {
      this.$Message.warning({ content: '获取手机号失败:' + err.message, duration: 3 })
    })
    queryServiceCenterByEmpNo({
      cityCode: this.studentDetail.cityCode
    }).then(data => {
      this.deptData = data
    }).catch(err => {
      this.$Message.error({
        content: '获取服务中心失败：' + err.message,
        duration: 5
      })
    })
  }
}
</script>

<style scoped lang="less">
  .name {
    display: inline-block;
    width: 100px;
    text-align: right;
  }
</style>
