<!--收支明细查询-->
<template>
  <div>
    <Form label-position="right" :label-width="90" inline style="background:#fff;padding-top: 24px" class="ivu-common-shadow">

      <FormItem label="日期">
        <DatePicker class="e-input" :editable="false" v-model="formData.startDateVal" @on-change="setStartTime" type="date" format="yyyy-MM-dd"></DatePicker> &nbsp;&nbsp;至&nbsp;&nbsp;
        <DatePicker class="e-input" :editable="false" v-model="formData.endDateVal" @on-change="setEndTime" type="date" format="yyyy-MM-dd"></DatePicker>
      </FormItem>
      <FormItem style="float: right">
        <Button type="primary" style="margin-right: 10px;" @click="searchTableData" :loading="tableLoading">查询</Button>
      </FormItem>
    </Form>
    <Table style="width: 100%;margin-top: 10px" :columns="cols" :data="tableData" :loading="tableLoading">
      <template slot-scope="{ row }"  slot="payStatus">
        {{row.payStatus === 0 ? '交易失败' : '交易成功'}}
      </template>
    </Table>
    <div style="margin: 10px;" :class="{ 'page-fix': tableData.length < 3 }">
      <div style="text-align: right;">
        <Page
          :total="formData.totalCount"
          :page-size="formData.pageSize"
          :current.sync="formData.pageNo"
          show-elevator
          show-sizer
          show-total
          @on-page-size-change="pageSizeChange"
          @on-change="changePage"></Page>
      </div>
    </div>
  </div>
</template>

<script>
import { queryTradeRecord } from '@/api/finance'
export default {
  data () {
    return {
      tableLoading: false,
      formData: {
        startDateVal: '',
        startDate: '',
        endDateVal: '',
        endDate: '',
        totalCount: 0,
        pageNo: 1,
        pageSize: 10
      },
      tableData: [],
      cols: [
        { title: '创建时间', align: 'center', key: 'createDate' },
        { title: '名称', align: 'center', key: 'createName' },
        { title: '交易单号', align: 'center', key: 'paybill' },
        { title: '交易金额', align: 'center', key: 'payPrice' },
        { title: '交易状态', align: 'center', slot: 'payStatus' },
        { title: '钱包账号', align: 'center', key: 'cardNo' },
        { title: '学员姓名', align: 'center', key: 'studentRealName' },
        { title: '收支', align: 'center', key: 'payTypeName' },
        { title: '操作人', align: 'center', key: 'creatorName' },
        { title: '教师', align: 'center', key: 'teacherNames' },
        { title: '备注', align: 'center', key: 'remark' }

      ],
      cityList: []
    }
  },
  props: {
    studentDetail: {
      type: Object,
      require: true
    }
  },
  methods: {
    /**
       * "查询"
       */
    searchTableData () {
      this.formData.pageNo = 1
      this.getTableData()
    },
    /**
       * 获取明细
       */
    getTableData () {
      if (this.formData.startDateVal && this.formData.endDateVal && this.formData.startDateVal > this.formData.endDateVal) {
        this.$Message.warning({
          content: '开始时间不能晚于结束时间',
          duration: 3
        })
        return
      }
      this.tableLoading = true
      queryTradeRecord(Object.assign({}, this.formData, {
        cityCode: this.studentDetail.cityCode,
        // studentCardId: this.studentDetail.stuCardId,
        studentId: this.studentDetail.studentId,
        studentUid: this.studentDetail.uid
      })).then(data => {
        this.formData.totalCount = data.totalCount
        this.tableData = data.records
      }).catch(err => {
        this.tableData = []
        this.formData.totalCount = 0
        this.$Message.error({
          content: '获取电子钱包-收支明细列表失败：' + err.message
        })
      }).finally(() => {
        this.tableLoading = false
      })
    },
    /**
       * 明细翻页
       * @param index
       */
    changePage (index) {
      this.formData.pageNo = index
      this.getTableData()
    },
    /**
       * 变更pageSize
       * @param size
       */
    pageSizeChange (size) {
      this.formData.pageNo = 1
      this.formData.pageSize = size
      this.getTableData()
    },
    /**
       * 计算两个时间之间的天数
       * @param date1 小日期
       * @param date2 大日期
       * @returns {number}
       * @constructor
       */
    DateMinus (date1, date2) {
      const start = new Date(date1)
      const end = new Date(date2)
      const days = end.getTime() - start.getTime()
      const day = parseInt(days / (1000 * 60 * 60 * 24))
      return day
    },
    validateDate (which) {
      if (this.formData.startDate && this.formData.endDate) {
        if (this.DateMinus(this.formData.startDate, this.formData.endDate) > 90) {
          this.$Message.warning({ content: '时间跨度不能大于90天，请重新选择', duration: 5 })
          if (which === 0) {
            this.formData.startDateVal = ''
            this.formData.startDate = ''
          } else if (which === 1) {
            this.formData.endDateVal = ''
            this.formData.endDate = ''
          } else {

          }
        }
      }
    },
    setStartTime (val) {
      this.formData.startDate = val
      this.validateDate(0)
    },
    setEndTime (val) {
      this.formData.endDate = val
      this.validateDate(1)
    }
  },
  mounted () {
    this.getTableData()
  }
}
</script>

<style scoped lang="less">
  .e-input {
    width: 160px;
  }
</style>
