<template>
  <div class="wrapper">
    <i-form v-ref:form-inline :model="formInline" inline :label-width="80">
         <Form-item label="编号">
            <i-input v-model="formInline.refundBillNo" placeholder="请输入"></i-input>
        </Form-item>
        <Form-item label="流水号范围">
            <i-input style="width:100px;display:inline-block;"  v-model="formInline.startSequence" placeholder="起" inline></i-input> 至
            <i-input style="width:100px;display:inline-block;" v-model="formInline.endSequence" placeholder="止" inline></i-input>
        </Form-item>
         <Form-item label="操作时间">
          <Date-picker  @on-change="changeDate" :value="timeValue1" type="daterange" format="yyyy/MM/dd" placement="bottom-end" placeholder="选择日期" style="width: 200px"></Date-picker>
        </Form-item>
        <Form-item label="区域">
          <i-select v-model="areaId" @on-change="changeAreaHandle" placeholder="请选择区域">
              <i-option v-for="(item, index) in areaList" :value="item.schoolId" :key="index">{{item.name}}</i-option>
          </i-select>
        </Form-item>
        <Form-item label="服务中心">
          <i-select v-model="formInline.serviceCenterName" placeholder="请选择服务中心">
            <i-option v-for="(item,index) in centerList" :key="index" :value="item.name">{{item.name}}</i-option>
          </i-select>
        </Form-item>
        <Form-item label="授课类型">
          <i-select v-model="formInline.classType" placeholder="请选择授课类型">
            <i-option value=""></i-option>
            <i-option value="1">在线</i-option>
            <i-option value="2">双师</i-option>
            <i-option value="3">面授</i-option>
          </i-select>
        </Form-item>
        <Form-item label="操作人">
          <i-input v-model="formInline.operatorName" placeholder="操作人"></i-input>
        </Form-item>
        <Form-item label="学员姓名">
            <i-input v-model="formInline.studentName" placeholder="学员姓名"></i-input>
        </Form-item>
        <Form-item label="退费单类型">
          <i-select v-model="refundType" placeholder="请选择退费单类型">
            <i-option value="1">班级退费单</i-option>
            <i-option value="2">书籍退费单</i-option>
          </i-select>
        </Form-item>
        <Form-item>
            <i-button type="primary" @click="handlSearch">查询</i-button>
            <i-button @click="handleReset">重置</i-button>
        </Form-item>
    </i-form>
    <div>
      <i-table :columns="columns" :data="tableList"></i-table>
    </div>
    <div class="page-box">
      <Page :total="total" size="small" show-elevator show-sizer @on-page-size-change="handleSizeChange" @on-change="handlePage"></Page>
    </div>
    <tal-class-detail
      :show="classDetail.show"
      :classId="classDetail.id"
      @close="classDetail.show = false">
    </tal-class-detail>
    <Modal
      :mask-closable="false"
      title="学员详情"
      v-model="stuDetail.show"
      :footer-hide="true"
      width="960px">
      <!--学员详情-->
      <tal-stu-details
        :cityCode="stuDetail.cityCode"
        :uid="stuDetail.uid"
        v-if="stuDetail.show"
        @close="stuDetail.show = false">
      </tal-stu-details>
    </Modal>
  </div>
</template>

<script>
/* eslint-disable */
import ClassMixin from '@/mixins/classDetail'
import StudentMixin from '@/mixins/stuDetail'
import { getAreaList, getServiceCenterList } from '@/api/bd'
import { queryRefundList, queryRefundList2, sendRefundVoucher, sendRefundVoucher2 } from '@/api/refundSearch'
import Cookie from 'js-cookie'
export default {
  mixins: [ClassMixin, StudentMixin],
  data () {
    return {
      name: '',
      areaList: [],
      centerList: [],
      cityCode: '00852',
      areaId: '',
      total: 0,
      timeValue1: [],
      refundType: '1',
      formInline: {
        cflat: '',
        pageNo: 1,
        pageSize: 10,
        studentId: '',
        studentName: '',
        refundBillNo: '',
        startSequence: '',
        endSequence: '',
        classType: '',
        operatorName: '',
        startTime: '',
        endTime: '',
        cityCode: '00852',
        serviceCenterName: ''
      },
      columns: [
        {
          title: '操作',
          key: 'action',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.handlePrint(params)
                  }
                }
              }, '打印')
            ])
          }
        },
        {
          title: '退费单号',
          key: 'refundBillNum',
          align: 'center'
        },
        {
          title: '学员姓名',
          key: 'studentRealName',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('a', {
                style: {
                  display: 'block',
                  color: '#008CF8'
                },
                on: {
                  click: () => {
                    this.showStuDetail(this.cityCode, params.row.studentUid)
                  }
                }
              }, params.row.studentRealName)
            ])
          }
        },
        {
          title: '班级名称',
          key: 'name',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('div', {
                style: {
                  'text-align': 'right'
                }
              }, [
                h('Tag', {
                  style: {
                    'display': params.row.businessType === 1 ? 'inline-block' : 'none'
                  },
                  props: {
                    color: 'green'
                  }
                }, '全国班')
              ]),
              h('a', {
                on: {
                  click: () => {
                    this.showClassDetail(params.row.classId)
                  }
                }
              }, params.row.className || '无')
            ])
          }
        },
        {
          title: '操作人',
          key: 'creatorName',
          align: 'center'
        },
        {
          title: '申请日期',
          key: 'details',
          align: 'center',
          render: (h, params) => {
            return h('div', params.row.details[0]['applyTime'])
          }
        },
        {
          title: '分期名称',
          key: 'name',
          align: 'center',
          render: (h, params) => {
            return h('div', params.row.details[0]['stageName'] || '-')
          }
        },
        {
          title: '应退金额',
          key: 'name',
          align: 'center',
          render: (h, params) => {
            return h('div', params.row.details[0]['shouldAmount'])
          }
        },
        {
          title: '实退金额',
          key: 'name',
          align: 'center',
          render: (h, params) => {
            return h('div', params.row.details[0]['actualAmount'])
          }
        },
        {
          title: '报班课次',
          key: 'name',
          align: 'center',
          render: (h, params) => {
            return h('div', params.row.details[0]['registNo'])
          }
        },
        {
          title: '退费课次',
          key: 'name',
          align: 'center',
          render: (h, params) => {
            return h('div', params.row.details[0]['registNo'])
          }
        },
        {
          title: '退费类型',
          key: 'name',
          align: 'center',
          render: (h, params) => {
            return h('div', params.row.details[0]['refundType'] || '-')
          }
        },
        {
          title: '缴费日期',
          key: 'name',
          align: 'center',
          render: (h, params) => {
            return h('div', params.row.details[0]['paymentTime'])
          }
        },
        {
          title: '缴费金额',
          key: 'name',
          align: 'center',
          render: (h, params) => {
            return h('div', params.row.details[0]['paymentAmount'])
          }
        }
      ],
      tableList: []
    }
  },
  mounted () {
    this.getCenter()
    this.handlSearch()
  },
  methods: {
    handlePrint (row) {
      if (this.refundType === '1') {
        sendRefundVoucher({
          'X-Ca-referer': 'RefundClass',
          cityCode: this.cityCode,
          refundBillId: row.row.refundBillId,
          isPrint: 1
        }).then(data => {
          this.printData(data)
        }).catch(err => {
          this.$Message.error({
            content: err.message
          })
        })
      } else {
        sendRefundVoucher2({
          refundId: row.row.refundBillId,
          cityCode: this.cityCode
        }).then(data => {
          this.printData(data)
        })
      }
    },
    printData (data) {
      var wind = window.open('', 'newwindow', 'height=700, width=1000, top=100, left=100, toolbar=no, menubar=no, scrollbars=no, resizable=no,location=n o, status=no')
      wind.document.body.innerHTML = data
      wind.print()
    },
    getCenter () {
      getAreaList({
        cityCode: this.cityCode
      }).then(res => {
        this.areaList = res
      }).catch(err => {
        this.$Message.error({
          content: '获取区域列表失败：' + err.message
        })
      })
    },
    changeAreaHandle () {
      getServiceCenterList({
        cityCode: this.cityCode,
        areaId: this.areaId
      }).then(res => {
        this.centerList = res
      }).catch(err => {
        this.$Message.error({
          content: '获取服务中心列表失败：' + err.message
        })
      })
    },
    handlSearch () {
      if (this.refundType === '1') {
        queryRefundList(this.formInline).then(res => {
          this.tableList = res.records
          this.total = res.totalCount
        }).catch(err => {
          this.tableList = []
          this.$Message.error({
            content: err.message
          })
        })
      } else {
        queryRefundList2(this.formInline).then(res => {
          this.tableList = res.records
          this.total = res.totalCount
        }).catch(err => {
          this.tableList = []
          this.$Message.error({
            content: err.message
          })
        })
      }
    },
    handleSizeChange (size) {
      this.formInline.pageSize = size
      this.handlSearch()
    },
    handlePage (current) {
      this.formInline.pageNo = current
      this.handlSearch()
    },
    changeDate (value) {
      console.log(value)
      this.formInline.startTime = new Date(value[0]).getTime()
      this.formInline.endTime = new Date(value[1]).getTime()
    },
    handleReset () {
      this.timeValue1 = []
      this.areaId = ''
      this.formInline = {
        cflat: '',
        pageNo: 1,
        pageSize: 10,
        studentId: '',
        studentName: '',
        refundBillNo: '',
        startSequence: '',
        endSequence: '',
        classType: '',
        operatorName: '',
        startTime: '',
        endTime: '',
        cityCode: this.cityCode,
        serviceCenterName: ''
      }
      this.handlSearch()
    }
  }
}
</script>
<style lang="less" scoped>
  .wrapper {
    padding: 20px;
    .page-box {
      text-align: right;
      margin-top:20px;
    }
  }
</style>
