<template>
  <div class="applyManager gary fixBorder">
    <TalStuSearch
      ref="stuSearch"
      addStu
      stuDetail
      :stu-card="queryData.card"
      :stu-city-code="queryData.cityCode"
      :showSendMsg="ifConsultantRole"
      @sendMsg="sendMsgEv"
      @crmInfo="setCrmInfo"
      @stuInfo="setStuInfo"></TalStuSearch>
    <Tabs style="margin-top: 10px; background: #fff;" type="card" :animated="false" @on-click="tabsChange" v-model="tabsName" name="applyTabs">
      <TabPane tab="applyTabs" label="班级查询" name="classQuery">
        <template v-if="showTab.classQuery">
          <TalApplyClassQuery
            ref="classQuery"
            :studentDetail="studentDetail"
            :years="yearList"
            :citys="allCityList"
            :classInfo="classInfo"
            @applyChoice='applyChoiceHandle'>
          </TalApplyClassQuery>
        </template>
      </TabPane>
      <TabPane tab="applyTabs" label="报班记录" name="classRecord" :disabled="hasStudentInfo">
        <template v-if="showTab.classRecord">
          <TalApplyClassRecord
            ref="classRecord"
            :yearList="yearList"
            :studentDetail="studentDetail"
            @report="report"
            @checkPayStatus="checkPayStatus">
          </TalApplyClassRecord>
        </template>
      </TabPane>
      <TabPane tab="applyTabs" label="订单中心" name="orderCenter" :disabled="hasStudentInfo">
        <template v-if="showTab.orderCenter">
          <TalApplyOrderCenter
            ref="orderCenter"
            :studentDetail="studentDetail"
            @checkPayStatus="checkPayStatus">
          </TalApplyOrderCenter>
        </template>
      </TabPane>
      <TabPane tab="applyTabs" label="候补记录" name="backupRecords" :disabled="hasStudentInfo">
        <template v-if="showTab.backupRecords">
          <TalApplyBackupRecords
            ref="backupRecords"
            :yearList="yearList"
            :studentDetail="studentDetail">
          </TalApplyBackupRecords>
        </template>
      </TabPane>
      <TabPane tab="applyTabs" label="预留转班名额记录" name="reserved" :disabled="hasStudentInfo">
        <template v-if="showTab.reserved">
          <TalApplyReserved
            ref="reserved"
            :yearList="yearList"
            :studentDetail="studentDetail">
          </TalApplyReserved>
        </template>
      </TabPane>
      <!--<TabPane tab="applyTabs" label="成绩查询" name="performance" :disabled="hasStudentInfo">-->
        <!--<template v-if="showTab.performance">-->
          <!--<TalApplyPerformance-->
            <!--ref="performance"-->
            <!--:yearList="yearList"-->
            <!--:studentDetail="studentDetail">-->
          <!--</TalApplyPerformance>-->
        <!--</template>-->
      <!--</TabPane>-->
      <TabPane tab="applyTabs" label="成绩查询" name="performance" :disabled="hasStudentInfo">
        <template v-if="showTab.performance">
          <TalScoreQuery ref="performance"
                         from="splat"
                         :stuId="studentDetail.studentId"
                         :cityCode="studentDetail.cityCode"
                         :stuCard="studentDetail.card"
                         :stuUid="studentDetail.uid"
                         :stuName="studentDetail.realName">
          </TalScoreQuery>
        </template>
      </TabPane>
      <TabPane tab="applyTabs" label="学员考勤" name="attendance" :disabled="hasStudentInfo">
        <template v-if="showTab.attendance">
          <TalApplyAttendance
            ref="attendance"
            @printAttendance="printAttendance"
            :studentDetail="studentDetail">
          </TalApplyAttendance>
        </template>
      </TabPane>
      <TabPane tab="applyTabs" label="优惠券" name="discount" :disabled="hasStudentInfo">
        <template v-if="showTab.discount">
          <TalDiscountCoupon
            ref="discount"
            :studentDetail="studentDetail">
          </TalDiscountCoupon>
        </template>
      </TabPane>
    </Tabs>
    <!--报名-->
    <TalApplyModal
      :show="applyShow"
      :isEdit="false"
      :classId="multiApply.classId"
      :stuId="multiApply.studentId"
      :stuCityCode="multiApply.cityCode"
      :creatorEmpNo="multiApply.empNo"
      @close="applyShow = false">
      <!--@success="getTableData('form')">-->
    </TalApplyModal>
    <Modal
      title="查找报名学员"
      width='800'
      :footer-hide="true"
      v-model="searchStuForApplyModal">
      <TalStuSearch
        ref="stuSearchForApply"
        :attendanceSearch='false'
        :showDetail='false'
        :showCrmInfo='false'
        choice-btn-text='报名'
        @stuInfo="setStuInfoForApplyHandle"></TalStuSearch>
    </Modal>
  </div>
</template>
<script>
import {
  getYearList,
  getCityList
} from '@/api/bd'
import getUserInfoByUidMixin from '@/components/mixin/getUserInfoByUid'
import Cookie from 'js-cookie'
export default {
  name: 'applyWorkbench',
  mixins: [ getUserInfoByUidMixin ],
  data () {
    return {
      applyShow: false,
      searchStuForApplyModal: false,
      tabsName: 'classQuery',
      currentTabName: 'classQuery',
      yearList: [],
      allCityList: [],
      hasStudentInfo: true,
      studentDetail: {},
      showTab: {
        classQuery: false,
        classRecord: false,
        orderCenter: false,
        performance: false,
        attendance: false,
        discount: false,
        reserved: false,
        backupRecords: false
      },
      loaded: false,
      reset: false,
      multiApply: {
        classId: '',
        studentId: '',
        cityCode: '',
        empNo: JSON.parse(Cookie.get('columbusUserInfo')).id
      },
      crmInfo: {}
    }
  },
  mounted () {
    this.getYearList()
    this.getCityList()
    if (!this.$route.query.card && !this.$route.query.cityCode) {
      this.setDefaultView()
    }
  },
  computed: {
    // 判断当前角色显示发送消息按钮
    ifConsultantRole () {
      const crmRoleId = this.$store.state.roleRelativeData.userInfo.crmRoleId
      const identityId = this.$store.state.roleRelativeData.roleInfo.identityId
      return crmRoleId === 5 && Number(identityId) === Number(this.crmInfo.belongIdentityId) && !!this.crmInfo.nimAccid
    }
  },
  methods: {
    sendMsgEv () {
      const sessionId = this.crmInfo.nimAccid
      if (!sessionId) {
        this.$message({
          message: '学员没有云信ID没法聊天',
          type: 'error'
        })
        return
      }
      if (this.$store.state.imchat.status === 'sucess') {
        this.$store.dispatch('imchat/startSession', sessionId)
      } else {
        this.$message({
          message: 'im聊天窗口没有初始化成功',
          type: 'error'
        })
      }
    },
    report () {
      this.$router.push({
        name: 'prescribeddaily'
      })
    },
    // 基础数据
    // 获取年级
    getYearList () {
      getYearList().then((data) => {
        this.yearList = data
      }).catch((err) => {
        this.$Message.warning({ content: '获取年份失败:' + err.message, duration: 5 })
      })
    },
    // 获取所在城市
    getCityList () {
      getCityList().then((data) => {
        this.allCityList = data
        // this.$store.commit("setAllSchoolList",data)
      }).catch((err) => {
        this.$Message.warning({ content: '获取所有城市失败:' + err.message, duration: 5 })
      })
    },
    tabsChange (name, forceChange) {
      console.log(`tabsChange: ${name}`)
      console.log(this.tabsName)
      let eventId = null
      switch (name) {
        case 'classQuery': // 点击班级查询
          eventId = '15-3607'; break
        case 'backupRecords': // 点击候补记录
          eventId = '15-3621'; break
        case 'discount': // 点击优惠券
          eventId = '15-3628'; break
      }
      if (eventId) {
        XesAnalytics.track(eventId, {})
      }
      if (name !== 'classQuery' && (this.currentTabName !== name || forceChange)) {
        this.currentTabName = name
        this.showTab[name] = true
        this.$nextTick(() => {
          this.$nextTick(() => {
            this.$refs[name].initView()
          })
        })
      } else {
        this.currentTabName = name
      }
    },
    setCrmInfo (info) {
      this.crmInfo = info
    },
    setStuInfo (info) {
      this.resetTabs()
      this.$nextTick(() => {
        this.studentDetail = info
        this.hasStudentInfo = false
        if (this.$route.query.tab) {
          this.$nextTick(() => {
            this.tabsName = this.$route.query.tab
            this.tabsChange(this.tabsName)
          })
        } else {
          this.currentTabName = 'classQuery'
          this.tabsName = 'classQuery'
          this.$nextTick(() => {
            this.setDefaultView()
          })
        }
      })
    },
    resetTabs () {
      this.showTab = {
        classQuery: true,
        classRecord: false,
        orderCenter: false,
        performance: false,
        attendance: false,
        discount: false,
        reserved: false,
        backupRecords: false
      }
    },
    setDefaultView () {
      this.showTab[this.tabsName] = true
      this.$nextTick(() => {
        this.$refs[this.tabsName].initView()
      })
    },
    checkPayStatus (status) {
      if (status) {
        this.tabsName = ''
        this.$nextTick(() => {
          this.tabsName = 'classRecord'
          this.tabsChange(this.tabsName)
        })
      } else {
        this.tabsName = 'orderCenter'
        this.tabsChange(this.tabsName)
      }
    },
    printAttendance (searchDate) {
      top.open(`/service/printAttendance?name=${this.studentDetail.realName}&card=${this.studentDetail.card}&sex=${this.studentDetail.sexName}&gradeName=${this.studentDetail.gradeName}&cityCode=${this.studentDetail.cityCode}&stuId=${this.studentDetail.studentId}&searchDate=${searchDate}&print=true`)
    },
    applyChoiceHandle (classInfo) {
      this.multiApply.classId = classInfo.id
      this.searchStuForApplyModal = true
    },
    setStuInfoForApplyHandle (stuInfo) {
      this.multiApply.studentId = stuInfo.studentId
      this.multiApply.cityCode = stuInfo.cityCode
      this.applyShow = true
    }
  }
}
</script>
<style scoped lang="less">
  .fixBorder /deep/ .ivu-table td,
  .fixBorder /deep/ .ivu-table th {
    border-bottom-width: 1.5px;
    border-right-width: 1.5px;
  }
  .applyManager {
    position: relative;
    flex: 1;
    padding: 10px;
    overflow-x: hidden;
  }
  .min500 {
    min-height: 500px;
  }
</style>
