<!--收支明细查询-->
<template>
  <div>
    <Form
      label-position="right"
      :label-width="90"
      inline
      style="background:#fff;padding-top: 24px"
      class="ivu-common-shadow"
    >
      <FormItem label="日期">
        <DatePicker
          class="e-input"
          :editable="false"
          v-model="formData.startDateVal"
          @on-change="setStartTime"
          type="date"
          format="yyyy-MM-dd"
        ></DatePicker>&nbsp;&nbsp;至&nbsp;&nbsp;
        <DatePicker
          class="e-input"
          :editable="false"
          v-model="formData.endDateVal"
          @on-change="setEndTime"
          type="date"
          format="yyyy-MM-dd"
        ></DatePicker>
      </FormItem>
      <FormItem style="float: right">
        <Button
          type="primary"
          style="margin-right: 10px;"
          @click="searchTableData"
          :loading="tableLoading"
        >查询</Button>
      </FormItem>
    </Form>
    <Table
      style="width: 100%;margin-top: 10px"
      :columns="cols"
      :data="tableData"
      :loading="tableLoading"
    ></Table>
    <div style="margin: 10px;" :class="{ 'page-fix': tableData.length < 3 }">
      <div style="text-align: right;">
        <Page
          :total="formData.totalCount"
          :page-size="formData.pageSize"
          :current.sync="formData.pageNo"
          show-elevator
          show-sizer
          show-total
          @on-page-size-change="pageSizeChange"
          @on-change="changePage"
        ></Page>
      </div>
    </div>
    <!-- 电子钱包提现 -->
    <Modal
      :mask-closable="false"
      title="电子钱包提现"
      width="960"
      v-model="walletWithdrawShow"
      :footer-hide="true"
    >
      <TalFinanceWalletWithdraw
        :studentDetail="studentDetail"
        :id="workOrderId"
        :payType="2"
        :operate="2"
        v-if="walletWithdrawShow"
        @close="walletWithdrawShow=false"
        @success="updateSuccess"
      ></TalFinanceWalletWithdraw>
    </Modal>
  </div>
</template>

<script>
import { queryBudgetDetail } from '@/api/finance'
import { getOaFormDetailUrl } from 'talApi/specialRefund'
export default {
  data () {
    return {
      tableLoading: false,
      formData: {
        startDateVal: '',
        startDate: '',
        endDateVal: '',
        endDate: '',
        totalCount: 0,
        pageNo: 1,
        pageSize: 10
      },
      tableData: [],
      cols: [
        { title: '创建时间', align: 'center', key: 'createDate' },
        { title: '流水号', align: 'center', key: 'payBill' },
        { title: '充值类型/备注', align: 'center', key: 'remark' },
        { title: '收支类型', align: 'center', key: 'budgetType' },
        { title: '收支金额', align: 'center', key: 'amount' },
        { title: '账户余额', align: 'center', key: 'afterBalance' },
        { title: '支付方式', align: 'center', key: 'payTypeName' },
        { title: '交易人', align: 'center', key: 'studentRealName' },
        { title: '操作人姓名', align: 'center', key: 'creatorName' },
        { title: '款项所属服务中心', align: 'center', key: 'serviceCenterName' },
        { title: '银行名称', align: 'center', key: 'bankName' },
        { title: '银行卡号', align: 'center', key: 'bankCardNo' },
        { title: '参考号', align: 'center', key: 'licenseKey' },
        { title: '缴费授权码', align: 'center', key: 'payedCode' },
        {
          title: '工单状态',
          align: 'center',
          key: 'payedCode',
          render: (h, params) => {
            // console.log('params', params)
            const obj = this.getStatusObj(params.row.status)
            return h('span', obj.label)
          }
        },
        {
          title: '操作',
          align: 'center',
          key: 'payedCode',
          render: (h, params) => {
            const obj = this.getStatusObj(params.row.status)
            const text = obj.check ? '查看工单' : obj.update ? '修改工单' : ''
            return h('a', {
              on: {
                click: () => {
                  if (obj.check) {
                    this.viewTicket(params.row.billCode)
                  } else if (obj.update) {
                    this.workOrderId = params.row.workOrderId
                    this.walletWithdrawShow = true
                  }
                }
              }
            }, text)
          }
        }

      ],
      cityList: [],
      workOrderId: '',
      walletWithdrawShow: false
    }
  },
  props: {
    studentDetail: {
      type: Object,
      require: true
    }
  },
  methods: {

    updateSuccess () {
      this.tableLoading = true
      setTimeout(() => {
        this.getTableData()
      }, 2000)
    },
    openUrlByNewWindow (url) {
      const aEle = document.createElement('a')
      aEle.href = url
      aEle.target = '_blank'
      const e = document.createEvent('MouseEvents')
      e.initEvent('click', true, true)
      aEle.dispatchEvent(e)
    },
    // 查看工单
    async viewTicket (billCode) {
      try {
        const params = {
          billCode,
          cityCode: this.studentDetail.cityCode
        }
        const url = await getOaFormDetailUrl(params)
        this.openUrlByNewWindow(url)
      } catch (error) {
        this.$Message.error({ content: '获取数据失败:' + error.message, duration: 5 })
      }
    },
    // 根据工单状态，返回文本和对应操作
    getStatusObj (status) {
      switch (status) {
        case 1:
          return {
            label: '审核中',
            check: true,
            update: false
          }
        case 2:
          return {
            label: '审核通过',
            check: true,
            update: false
          }
        case 3:
          return {
            label: '驳回',
            check: false,
            update: true
          }
        default:
          return {
            label: '',
            check: false,
            update: false
          }
      }
    },
    /**
     * "查询"
     */
    searchTableData () {
      this.formData.pageNo = 1
      this.getTableData()
    },
    /**
     * 获取明细
     */
    getTableData () {
      if (this.formData.startDateVal && this.formData.endDateVal && this.formData.startDateVal > this.formData.endDateVal) {
        this.$Message.warning({
          content: '开始时间不能晚于结束时间',
          duration: 3
        })
        return
      }
      this.tableLoading = true
      queryBudgetDetail(Object.assign({}, this.formData, {
        cityCode: this.studentDetail.cityCode,
        studentCardId: this.studentDetail.stuCardId,
        studentId: this.studentDetail.studentId,
        studentUid: this.studentDetail.uid
      })).then(data => {
        this.formData.totalCount = data.totalCount
        this.tableData = data.records
      }).catch(err => {
        this.tableData = []
        this.formData.totalCount = 0
        this.$Message.error({
          content: '获取电子钱包-收支明细列表失败：' + err.message
        })
      }).finally(() => {
        this.tableLoading = false
      })
    },
    /**
     * 明细翻页
     * @param index
     */
    changePage (index) {
      this.formData.pageNo = index
      this.getTableData()
    },
    /**
     * 变更pageSize
     * @param size
     */
    pageSizeChange (size) {
      this.formData.pageNo = 1
      this.formData.pageSize = size
      this.getTableData()
    },
    /**
     * 计算两个时间之间的天数
     * @param date1 小日期
     * @param date2 大日期
     * @returns {number}
     * @constructor
     */
    DateMinus (date1, date2) {
      const start = new Date(date1)
      const end = new Date(date2)
      const days = end.getTime() - start.getTime()
      const day = parseInt(days / (1000 * 60 * 60 * 24))
      return day
    },
    validateDate (which) {
      if (this.formData.startDate && this.formData.endDate) {
        if (this.DateMinus(this.formData.startDate, this.formData.endDate) > 90) {
          this.$Message.warning({ content: '时间跨度不能大于90天，请重新选择', duration: 5 })
          if (which === 0) {
            this.formData.startDateVal = ''
            this.formData.startDate = ''
          } else if (which === 1) {
            this.formData.endDateVal = ''
            this.formData.endDate = ''
          } else {

          }
        }
      }
    },
    setStartTime (val) {
      this.formData.startDate = val
      this.validateDate(0)
    },
    setEndTime (val) {
      this.formData.endDate = val
      this.validateDate(1)
    }
  },
  mounted () {
    this.getTableData()
  }
}
</script>

<style scoped lang="less">
.e-input {
  width: 160px;
}
</style>
