<template>
  <div>
    <Qrcode v-if="code" class="qr-code-img" :text="code" :correctLevel="0" :size="200" :margin="10"></Qrcode>
    <div class="stu-info">
      <p>学员姓名：{{name}}</p>
      <p>就读年级：{{grade}}</p>
      <p>学员编号：{{stuNo}}</p>
    </div>
  </div>
</template>

<script>
import Qrcode from 'vue-qr'
export default {
  name: 'PrintQR',
  components: {
    Qrcode
  },
  data () {
    return {
      code: '',
      name: '',
      grade: '',
      stuNo: ''
    }
  },
  mounted () {
    this.code = this.$route.query.code
    this.name = this.$route.query.name
    this.grade = this.$route.query.grade
    this.stuNo = this.$route.query.stuNo
    setTimeout(() => {
      window.print()
    }, 100)
  }
}
</script>

<style scoped>
  .stu-info {
    width: 200px;
    font-size: 16px;
    margin-left: 10px;
    p {
      margin-top: 10px;
    }
  }
</style>
