<template>
  <!--电子钱包余额提现-->
  <div class="balance-withdrawal wrapper">
    <Card class="balance-title">
      <Spin fix v-if="spinShow" size="large"></Spin>
      <Row>
        <Col span="6">
          <span>学员姓名：</span>
          <span>{{studentData.studentName || '--'}}</span>
        </Col>
        <Col span="8">
          <span>电子钱包账号：</span>
          <span>{{titleData.cardNo || '--'}}</span>
        </Col>
        <Col span="6">
          <span>年级：</span>
          <span>{{studentData.gradeName || '--'}}</span>
        </Col>
      </Row>
      <Row style="margin-top: 10px;">
        合计：可提现余额：{{titleData.withdrawalBalance|formatMoneyFilter}}元，其中刷卡共计{{titleData.swipeBalance|formatMoneyFilter}}元，现金共计{{titleData.cashBalance|formatMoneyFilter}}元，支票共计{{titleData.checkBalance|formatMoneyFilter}}元。
      </Row>
    </Card>
    <!--<Divider />-->
    <Form :label-width="100" inline :model="formItem" :rules="ruleItem" style="margin-top: 20px;" ref="formItem">
      <FormItem  label="缴费方式：" prop="status">
        <Select v-model="formItem.status" placeholder="缴费方式" class="w120">
          <Option v-for="(item, index) in statusList" :key="index" :value="item.value">{{ item.label }}</Option>
        </Select>
      </FormItem>
      <FormItem style="float: right">
        <Button type="primary" @click="searchData" :loading="tableLoading">查询</Button>
        <Button type="primary" class="ml10" @click="batchRefund">批量退费</Button>
      </FormItem>
    </Form>
    <Table
      :columns="tableColumns"
      :data="tableData"
      :loading="tableLoading"
      @on-select="handleSelect"
      @on-select-cancel="handleCancel"
      @on-select-all-cancel="handleCancleAll"
      @on-select-all="handleSelectAll"
    >
      <template slot-scope="{ row, index }" slot="payPrice">
        <span>{{ row.payPrice|formatMoneyFilter}}</span>
      </template>
      <template slot-scope="{ row, index }" slot="returnableBalance">
        <span>{{ row.returnableBalance|formatMoneyFilter}}</span>
      </template>
      <template slot-scope="{ row, index }" slot="originalBalance">
        <span>{{ row.originalBalance|formatMoneyFilter}}</span>
      </template>
      <template slot-scope="{ row, index }" slot="serviceCenterName">
        <span>{{ row.serviceCenterName || '--'}}</span>
      </template>
      <template slot-scope="{ row, index }" slot="createTime">
        <span>{{ row.createTime || '--'}}</span>
      </template>
      <template slot-scope="{ row, index }" slot="remark">
        <span>{{ row.remark || '--'}}</span>
      </template>
      <template slot-scope="{ row, index }" slot="payType">
        <span>{{ row.payType|filterPayType}}</span>
      </template>
    </Table>
    <!-- 电子钱包提现 -->
    <Modal
      :mask-closable="false"
      title="电子钱包提现"
      width="960"
      v-model="walletWithdrawShow"
      :footer-hide="true"
    >
      <TalFinanceWalletWithdraw v-if="walletWithdrawShow" @close="walletWithdrawShow=false" :details="selectList" :payType="formItem.status" :operate="1" :studentDetail="studentDetail" @success="withSuccess"></TalFinanceWalletWithdraw>
    </Modal>
  </div>
</template>

<script>
import { queryWithdrawal } from '@/api/finance'
export default {
  name: 'balanceWithdrawal',
  data () {
    return {
      titleData: {
        withdrawalBalance: 0,
        cardNo: '',
        swipeBalance: 0,
        cashBalance: 0,
        checkBalance: 0
      },
      formItem: {
        status: 1
      },
      statusList: [{
        value: 1,
        label: '现金'
      }, {
        value: 2,
        label: '刷卡'
      }, {
        value: 3,
        label: '支票'
      }],
      ruleItem: {
        status: [
          { required: true, type: 'number', message: '请选择缴费方式', trigger: 'change' }
        ]
      },
      tableColumns: [
        { type: 'selection', width: 50, align: 'center' },
        { type: 'index', width: 50, align: 'center', title: '序号' },
        { slot: 'payPrice', width: 90, align: 'center', title: '缴费金额（元）' },
        { slot: 'returnableBalance', width: 90, align: 'center', title: '可退余额（元）' },
        { slot: 'payType', width: 80, align: 'center', title: '缴费方式' },
        { slot: 'originalBalance', width: 100, align: 'center', title: '卡内原余额' },
        { slot: 'serviceCenterName', width: 80, align: 'center', title: '缴费地点' },
        { slot: 'createTime', width: 170, align: 'center', title: '缴费时间' },
        { slot: 'remark', minWidth: 200, align: 'center', title: '备注' }
      ],
      tableData: [],
      tableLoading: false,
      selectList: [],
      studentData: {},
      spinShow: false,
      walletWithdrawShow: false,
      studentDetail: {}
    }
  },
  filters: {
    filterPayType (val) {
      let obj = {
        1: '现金',
        2: '刷卡',
        3: '支票'
      }
      return obj[val] || '--'
    }
  },
  beforeMount () {
    this.studentData = { ...this.$route.query }
    // 获取表头和表格数据
    this.spinShow = true
    this.tableLoading = true
    this.getTitleData(1, () => {
      this.spinShow = false
      this.tableLoading = false
    })
  },
  methods: {
    searchData () {
      // 查询表格数据
      this.$refs['formItem'].validate((valid) => {
        if (valid) {
          this.tableLoading = true
          this.getTitleData(2, () => {
            this.tableLoading = false
          })
        }
      })
    },
    getTitleData (type, callback) {
      queryWithdrawal({
        studentId: this.studentData.studentId,
        payType: this.formItem.status,
        cityCode: this.studentData.cityCode
      }).then(data => {
        this.selectList = []
        if (type === 1) {
          if (!data) return
          this.titleData = data
          this.tableData = data.details
        } else if (type === 2) {
          this.tableData = data ? data.details : []
        }
      }).catch(err => {
        this.$Message.error({
          content: err.message,
          duration: 5
        })
      }).finally(() => {
        if (callback) callback()
      })
    },
    batchRefund () {
      if (this.selectList.length === 0) {
        this.$Message.warning('请选择批量退费数据')
        return
      }
      this.selectList.map(item => {
        item.studentId = this.studentData.studentId
        item.studentName = this.studentData.studentName
        item.studentCard = this.titleData.cardNo
        item.studentUid = this.studentData.studentUid
        return item
      })
      this.studentDetail = {
        ...this.studentData,
        studentCard: this.titleData.cardNo
      }
      this.walletWithdrawShow = true
    },
    handleSelect (selection, row) {
      // 单选
      this.tableData.find(item => item.financeId === row.financeId)._checked = true
      this.selectList.push(row)
    },
    handleCancel (selection, row) {
      // 取消单选
      this.tableData.find(item => item.financeId === row.financeId)._checked = false
      this.selectList = this.selectList.filter(item => item.financeId !== row.financeId)
    },
    handleSelectAll () {
      // 全选
      this.tableData.map(item => {
        item._checked = true
        return item
      })
      this.selectList = [...this.tableData]
    },
    handleCancleAll () {
      // 取消全选
      this.tableData.map(item => {
        item._checked = false
        return item
      })
      this.selectList = []
    },
    withSuccess () {
      this.spinShow = true
      this.tableLoading = true
      this.getTitleData(1, () => {
        this.tableLoading = false
        this.spinShow = false
      })
    }
  }
}
</script>
<style scoped lang="less">
  .balance-withdrawal{
    padding: 20px;
    width: 100%;
    overflow-y: auto;
    .w120{
      width: 120px;
    }
    .ml10{
      margin-left: 10px;
    }
  }
  .balance-title{
    position: relative;
  }

</style>
<style lang="less">
  .balance-withdrawal{
    .ivu-card{
      font-size: 14px;
    }
  }
</style>
