<template>
  <Layout id="TalClassAttendance" style="width: 100%;" class="gary">
    <div class="container">
      <Form ref="form" :model="formData" :rules="ruleForm" label-position="right" :label-width="90" inline class="ivu-common-shadow" style="padding-top: 18px;background: #fff">
          <FormItem prop="cityCode" label="城市">
            <Select
              v-model="formData.cityCode"
              filterable
              style="width: 120px;"
              @on-change="cityYearChange">
              <Option v-for="(option,index) in cityWithoutAllCity" :value="option.id" :key="index">{{option.name}}</Option>
            </Select>
          </FormItem>
          <FormItem prop="year" label="年份">
            <Select v-model="formData.year" style="width: 120px;" @on-change="cityYearChange">
              <Option v-for="(item,index) in yearList" :value="item" :key="index">{{ item }}</Option>
            </Select>
          </FormItem>
          <FormItem prop="termId" label="学期">
            <Select v-model="formData.termId" style="width: 120px;" @on-change="termChange">
              <Option v-for="(item,index) in termList" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem prop="gradeTypeId" label="学部">
            <Select v-model="formData.gradeTypeId" style="width: 120px;" @on-change="gradeTypeChange" clearable>
              <Option v-for="(item,index) in gradeTypeList" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem label="年级">
            <Select v-model="formData.gradeId" style="width: 120px;" @on-change="gradeChange" clearable>
              <Option v-for="(item,index) in gradeList" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem label="学科">
            <Select v-model="formData.subjectId" style="width: 120px;" @on-change="subjectChange" clearable filterable>
              <Option v-for="(item,index) in subjectList" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem label="班次">
            <Select
              v-model="formData.levelId"
              clearable
              filterable
              style="width: 120px;">
              <Option v-for="(option,index) in levelList" :value="option.id" :key="index">{{option.name}}</Option>
            </Select>
          </FormItem>
          <FormItem label="老师姓名">
            <Select
              v-model="formData.teacherId"
              filterable
              clearable
              remote
              @on-change="optionChange"
              :remote-method="getTeacher"
              @on-clear="clearTeacher"
              label-in-value
              style="width: 120px;"
              :loading="loading1">
              <Option v-for="option in teacherNameList" :value="option.teaId" :key="option.teaId">{{option.sysName}}</Option>
            </Select>
          </FormItem>
          <FormItem label="是否入学测试">
            <Select v-model="formData.isSchoolTest" clearable style="width:120px">
              <Option v-for="(item,index) in yesOrNOList" :value="item.value" :key="index">{{ item.label }}</Option>
            </Select>
          </FormItem>
          <FormItem prop="areaId" label="区域">
            <Select v-model="formData.areaId" @on-change="changeArea" style="width:120px" filterable>
              <Option v-for="item in areaList" :value="item.schoolId" :key="item.schoolId">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem prop="serviceCenterId" label="服务中心">
            <Select v-model="formData.serviceCenterId" @on-change="changeService" style="width:120px" filterable>
              <Option v-for="(item,index) in serviceCenterList" :value="item.schoolId" :key="index">{{ item.name }}
              </Option>
            </Select>
          </FormItem>
          <FormItem label="教学点">
            <Select v-model="formData.venueId" clearable style="width:120px" filterable>
              <Option v-for="(item,index) in venueList" :value="item.schoolId" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem label="上课时间类型">
            <Select v-model="formData.classtimeTimeClassify" clearable style="width:120px">
              <Option v-for="(item,index) in classtimeTypeList" :value="item.name" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem label="上课时间">
            <Select v-model="formData.classtimeTimePeriod" clearable style="width:120px">
              <Option v-for="(item,index) in classtimeList" :value="item.name" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem label="是否报同期班">
            <Select v-model="formData.isReg" clearable style="width:120px">
              <Option v-for="(item,index) in yesOrNOList" :value="item.value" :key="index">{{ item.label }}</Option>
            </Select>
          </FormItem>
          <FormItem label="学员编号">
            <Input clearable placeholder="请输入" style="width: 120px" v-model.trim="formData.studentCard"/>
          </FormItem>
          <FormItem label="学员姓名">
            <Input clearable placeholder="请输入" style="width: 120px" v-model.trim="formData.studentName"/>
          </FormItem>
          <FormItem label="创建人">
            <Select
              v-model="formData.creatorId"
              filterable
              clearable
              remote
              :remote-method="queryEmployeeListByFuzzyName"
              label-in-value
              style="width: 150px;"
            >
              <Option
                v-for="item in employeeList"
                :value="item.empId"
                :key="item.empId"
              >{{item.remark }}</Option>
            </Select>
        </FormItem>
          <FormItem label="创建日期">
            <DatePicker :editable="false" type="date" v-model="formData.startCreateDateVal" @on-change="startCreateDateChange" style="width:120px"></DatePicker>
            &nbsp;&nbsp;至&nbsp;&nbsp;
            <DatePicker :editable="false" type="date" v-model="formData.endCreateDateVal" @on-change="endCreateDateChange" style="width:120px"></DatePicker>
          </FormItem>
        <FormItem label="剩余课次">
          <InputNumber :max="100" :min="0" style="width: 60px" v-model="formData.classStartCount"/>
          &nbsp;&nbsp;至&nbsp;&nbsp;
          <InputNumber :max="100" :min="0" style="width: 60px" v-model="formData.classEndCount"/>
        </FormItem>
        <FormItem label="剩余名额">
          <InputNumber :max="100" :min="0" style="width: 60px" v-model="formData.minRemainNum"/>
          &nbsp;&nbsp;至&nbsp;&nbsp;
          <InputNumber :max="100" :min="0" style="width: 60px" v-model="formData.maxRemainNum"/>
        </FormItem>
          <div style="text-align: right;background: #ffffff">
            <FormItem>
              <Button type="primary" style="margin-right: 10px;" @click="search('form')">查询班级</Button>
              <Button style="margin-right: 10px;" @click="resetField">重置</Button>
            </FormItem>
          </div>
      </Form>
      <div style="margin-top: 9px;color: red;font-size: 14px">
         为保护学员信息，导出的EXCEL中已删除电话号码，如需学员手机号请用万象平台导出
      </div>
      <div style="margin-top: 9px">
        <Table
          style="width: 100%"
          ref="selection"
          :columns="tableColumns"
          :data="tableData"
          @on-selection-change="selectionChange"
          :loading="tableLoading"></Table>
      </div>
      <div style="margin: 10px;" :class="{ 'page-fix': tableData.length < 3 }">
        <div style="float: left">
           <Button :loading="exportLoading" @click="exportExcel">导出所有</Button>
           <Button :disabled="!selectedRows.length" @click="batchRemove">对选中项批量删除</Button>
        </div>
        <div style="float: right;">
          <Page
            :total="pageTotal"
            :page-size="currentPageSize"
            :current="currentPageNum"
            show-elevator
            show-sizer
            show-total
            @on-page-size-change="pageSizeChange"
            @on-change="changePage"></Page>
        </div>
      </div>
    </div>
    <tal-class-detail
      :show="classDetail.show"
      :classId="classDetail.id"
      @close="classDetail.show = false">
    </tal-class-detail>
    <tal-teacher-details
      ref="teacherRef"
      :schoolCode="teacherDetail.cityCode"
      :teacherId="teacherDetail.id"
      :type="teacherDetail.type"
      :creatorEmpNo="teacherDetail.empNo"
    ></tal-teacher-details>
    <Modal
      :mask-closable="false"
      title="学员详情"
      v-model="stuDetail.show"
      :footer-hide="true"
      width="960px">
      <!--学员详情-->
      <tal-stu-details
        :cityCode="stuDetail.cityCode"
        :uid="stuDetail.uid"
        v-if="stuDetail.show"
        @close="stuDetail.show = false">
      </tal-stu-details>
    </Modal>
    <!--报名-->
    <TalApplyModal
      :show="applyShow"
      :isEdit="false"
      :classId="row.classId"
      :stuId="studentDetail.studentId"
      :stuCityCode="studentDetail.cityCode"
      :creatorEmpNo="empNo"
      @close="applyShow = false"
      @success="getTableData('form')">
    </TalApplyModal>
    <!--转班-->
    <TalReverseModal
      v-if="reverseShow"
      v-model="reverseShow"
      @close="reverseShow = false"
      @success="getTableData('form')"
      :studentDetail="studentDetail"
      :reverseTargetData="row"
      :reverseData="reverseData"
      :reverseTypeList="reverseTypeList">
    </TalReverseModal>
    <!-- 课次信息 -->
    <TalSchooltime ref="talSchooltime" />
  </Layout>
</template>

<script>
import { getStrLength } from '@/utils/utils'
import BaseDataMixin from '@/mixins/baseData'
import ClassMixin from '@/mixins/classDetail'
import TeacherMixin from '@/mixins/teacherDetail'
import StudentMixin from '@/mixins/stuDetail'
import {
  queryWaitList,
  querySourceClass,
  getChangeClassType,
  deleteAlternateRecord,
  batchRemoveWait,
  exportWaitListExportExcel
} from '@/api/register'
import {
  queryServiceCenterByEmpNo
} from '@/api/clazz'
import { setFrom, queryEmployeeListByFuzzyName } from '@/api/bd'
import Cookie from 'js-cookie'
const defaultSetQuery = {
  cityCode: '', // 必填
  year: new Date().getFullYear() + '', // 必填
  gradeTypeId: '',
  termId: '',
  gradeId: '',
  subjectId: '',
  levelId: '', // 班次
  studentCard: '', // 学员编号
  studentName: '', // 学员姓名
  teacherId: '', // 老师id
  isSchoolTest: null, // 是否入学测试(0,否，1 是)
  areaId: '',
  serviceCenterId: '',
  venueId: '', // 教学点
  classtimeTimeClassify: '', // 上课时间类型
  classtimeTimePeriod: '', // 上课时间
  startCreateDate: '',
  endCreateDate: '',
  startCreateDateVal: '',
  endCreateDateVal: '',
  classStartCount: null,
  classEndCount: null,
  minRemainNum: null,
  maxRemainNum: null,
  creatorId: '' // 创建人id
}
export default {
  name: 'backupBatch',
  mixins: [BaseDataMixin, ClassMixin, TeacherMixin, StudentMixin],
  data () {
    return {
      empNo: '',
      formData: Object.assign({}, defaultSetQuery),
      loading1: false, // 教师姓名
      tableLoading: false,
      oldSearchName: '',
      currentPageNum: 1, // 当前页
      currentPageSize: 10, // 显示条数
      pageTotal: 0, // 总条数
      cityWithoutAllCity: [],
      allCityList: [], // 分校
      termList: [], // 学期
      gradeTypeList: [], // 年部
      subjectList: [], // 学科
      gradeList: [], // 年级
      levelList: [], // 班次
      teacherNameList: [], // 教师姓名
      areaList: [], // 区域
      serviceCenterList: [], // 服务中心
      venueList: [], // 教学点
      yesOrNOList: [
        { value: 0, label: '否' },
        { value: 1, label: '是' }
      ], // 是否进行入学测试
      ruleForm: {
        cityCode: [{ required: true, message: '分校不能为空', trigger: 'change' }],
        year: [{ required: true, message: '年份不能为空', trigger: 'change' }],
        termId: [{ required: true, message: '学期不能为空', trigger: 'change' }],
        areaId: [{ required: true, message: '地区不能为空', trigger: 'change' }],
        serviceCenterId: [{ required: true, message: '服务中心不能为空', trigger: 'change' }]
      },
      tableData: [], // 表格数据
      tableColumns: [
        { type: 'selection', width: 60, align: 'center' },
        {
          title: '操作',
          align: 'center',
          width: 100,
          render: (h, params) => {
            return h('div', [
              h('a', {
                style: {
                  display: params.row.isReg === 0 ? 'inline-block' : 'none',
                  marginRight: '5px',
                  color: '#008CF8'
                },
                on: {
                  click: () => {
                    this.applyClass(params)
                  }
                }
              }, '报名'),
              h('a', {
                style: {
                  display: params.row.isReg === 1 ? 'inline-block' : 'none',
                  marginRight: '5px',
                  color: '#008CF8'
                },
                on: {
                  click: () => {
                    this.reverseClass(params)
                  }
                }
              }, '转入此班'),
              h('a', {
                style: {
                  marginRight: '5px',
                  color: '#008CF8'
                },
                on: {
                  click: () => {
                    this.removeWaitRecords(params)
                  }
                }
              }, '删除')
            ])
          }
        },
        { title: '年份', key: 'year', align: 'center' },
        { title: '班级名称',
          key: 'className',
          align: 'center',
          width: 160,
          render: (h, params) => {
            return h('div', [
              h('div', {
                style: {
                  'text-align': 'right'
                }
              }, [
                h('Tag', {
                  style: {
                    'display': params.row.businessType === 1 ? 'inline-block' : 'none'
                  },
                  props: {
                    color: 'green'
                  }
                }, '全国班')
              ]),
              h('a', {
                on: {
                  click: () => {
                    this.showClassDetail(params.row.classId)
                  }
                }
              }, params.row.className)
            ])
          }
        },
        { title: '学科', key: 'subjectNames', align: 'center' },
        { title: '教室', key: 'classroomName', align: 'center' },
        { title: '上课时间',
          key: 'classtimeNames',
          width: '120',
          align: 'center',
          render: (h, params) => {
            return h('a', {
              on: {
                click: async () => {
                  this.showSchooltime({ cityId: this.formData.cityCode, id: params.row.classId })
                }
              }
            }, getStrLength(params.row.classtimeNames, 15, true))
          }
        },
        { title: '教师',
          key: 'teacherNames',
          align: 'center',
          render: (h, params) => {
            const tNames = params.row.teacherNames.split(',')
            const tIds = params.row.teacherIds.split(',')
            return h('div', tNames.map((n, index) => {
              return h('a', {
                on: {
                  click: () => {
                    this.showTeacherDetail(this.formData.cityCode, tIds[index], '0')
                  }
                }
              }, n + ' ')
            }))
          }
        },
        { title: '限额', key: 'maxPersons', align: 'center' },
        { title: '剩余名额', key: 'remainNum', align: 'center' },
        { title: '学员编码', key: 'studentCard', align: 'center' },
        {
          title: '学员名称',
          key: 'studentRealName',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('a', {
                style: {
                  display: 'block',
                  color: '#008CF8'
                },
                on: {
                  click: () => {
                    this.showStuDetail(this.formData.cityCode, params.row.studentUid)
                  }
                }
              }, params.row.studentName)
            ])
          }
        },
        { title: '创建人', align: 'center', key: 'creatorName' },
        { title: '创建时间', align: 'center', key: 'createTime' },
        { title: '变动记录',
          key: 'classChangeName',
          align: 'center',
          width: 150,
          render: (h, params) => {
            return h('Poptip', {
              style: {
                display: 'inline-block',
                marginRight: '5px'
              },
              props: {
                trigger: 'hover',
                width: '300',
                wordWrap: true,
                transfer: true,
                content: params.row.classChangeName,
                placement: 'top-end'
              }
            }, params.row.businessType !== 1 ? getStrLength(params.row.classChangeName, 20, true) : '——')
          }
        },
        { title: '候补原因', align: 'center', key: 'description' },
        { title: '是否报同期班',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.isReg === 0 ? '未报' : '已报')
          }
        }
      ],
      studentDetail: {
        cityCode: '',
        studentId: ''
      },
      applyShow: false,
      reverseShow: false,
      reverseData: [], // 转班传的数据
      reverseTypeList: [],
      row: {
        classId: ''
      }, // 当前操作的行
      selectedRows: [],
      exportLoading: false,
      employeeList: [] // 创建人模糊搜索
    }
  },
  created () {
    this.getYearListHandler()
    this.getCityListHandler()
    this.getTimeTypeListHandler()
    this.getTimeListHandler()
    this.empNo = JSON.parse(Cookie.get('columbusUserInfo')).id
    setFrom({
      apiName: 'waitBatchDealPage'
    })
  },
  watch: {
    allCityList (val) {
      if (val && val.length) {
        this.formData.cityCode = val[0].id
        this.cityYearChange(this.formData.cityCode)
      }
      this.cityWithoutAllCity = val.filter(obj => obj.id !== '1111')
    }
  },
  methods: {
    // 远程搜索接待顾问
    queryEmployeeListByFuzzyName (userName) {
      const params = {
        name: userName,
        cityCode: this.formData.cityCode
      }
      queryEmployeeListByFuzzyName(params).then(res => {
        this.employeeList = Object.freeze(res)
      }).catch(err => {
        this.$Message.error({ content: err.message, duration: 5 })
      })
    },
    // 基本数据
    getBaseData () {
      // 查询类型：t 学期，gt 年部，g 年级，s 学科,cd 班次级别，c 班次
      let toNum = {
        cityCode: this.formData.cityCode
      }
      this.getAreaListHandler(toNum).then(res => {
        this.areaList = res
        queryServiceCenterByEmpNo({
          cityCode: this.formData.cityCode,
          empNo: this.empNo
        }).then(data => {
          this.formData.areaId = data.areaId
          this.changeArea(data.areaId, data.serviceCenterId)
        }).catch(err => {
          this.$Message.error('获取当前所在服务中心失败:' + err.message)
        })
      }).catch(err => {
        this.$Message.error('获取区域失败:' + err.message)
      })
    },
    // 根据状态清空 学期，年部，年级，学科，班次数据
    clearList (type) {
      switch (type) {
        case 'school':
          this.termList = []
          this.formData.termId = ''
          this.gradeTypeList = []
          this.formData.gradeTypeId = ''
          this.gradeList = []
          this.formData.gradeId = ''
          this.subjectList = []
          this.formData.subjectId = ''
          this.levelList = []
          this.formData.levelId = ''
          this.formData.areaId = ''
          this.areaList = []
          this.formData.serviceCenterId = ''
          this.serviceCenterList = []
          this.formData.venueId = ''
          this.venueList = []
          break
        case 'term' :
          this.formData.gradeTypeId = ''
          this.formData.gradeId = ''
          this.formData.subjectId = ''
          this.formData.levelId = ''
          this.gradeTypeList = []
          this.gradeList = []
          this.subjectList = []
          this.levelList = []
          break
        case 'gradeType' :
          this.formData.gradeId = ''
          this.formData.subjectId = ''
          this.formData.levelId = ''
          this.gradeList = []
          this.subjectList = []
          this.levelList = []
          break
        case 'grade' :
          this.formData.subjectId = ''
          this.formData.levelId = ''
          this.subjectList = []
          this.levelList = []
          break
        case 'subject' :
          this.formData.levelId = ''
          this.levelList = []
          break
      }
    },
    cityYearChange (val) { // 改变城市
      this.clearList('school')
      if (!val) {
        return false
      }
      if (!this.formData.cityCode) {
        this.$Message.warning('请选择城市')
        return false
      }
      this.getBaseData()
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        queryType: 't'
      }
      this.getLinkageHandler(req).then(res => {
        this.termList = res
      }).catch(err => {
        this.$Message.error('获取学期失败:' + err.message)
      })
    },
    termChange (val) { // 改变学期
      this.clearList('term')
      if (!val) return
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        queryType: 'gt'
      }
      this.getLinkageHandler(req).then(res => {
        this.gradeTypeList = res
      }).catch(err => {
        this.$Message.error('获取年部失败:' + err.message)
      })
    },
    gradeTypeChange (val) { // 改变年部
      this.clearList('gradeType')
      if (!val) return
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        gtIds: this.formData.gradeTypeId,
        queryType: 'g'
      }
      this.getLinkageHandler(req).then(res => {
        this.gradeList = res
      }).catch(err => {
        this.$Message.error('获取年级失败:' + err.message)
      })
    },
    gradeChange (val) { // 改变年级
      this.clearList('grade')
      if (!val) return
      let baseSubject = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        gtIds: this.formData.gradeTypeId,
        gradeIds: this.formData.gradeId,
        queryType: 's'
      }
      this.getLinkageHandler(baseSubject).then(res => {
        this.subjectList = res
      }).catch(err => {
        this.$Message.error('获取学科失败:' + err.message)
      })
    },
    subjectChange (val) { // 改变学科
      this.clearList('subject')
      if (!val) return
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        gtIds: this.formData.gradeTypeId,
        gradeIds: this.formData.gradeId,
        subjectId: this.formData.subjectId,
        queryType: 'c'
      }
      this.getLinkageHandler(req).then(res => {
        this.levelList = res
      }).catch(err => {
        this.$Message.error('获取班次失败:' + err.message)
      })
    },
    // 主讲老师搜索
    getTeacher (query) {
      if (!this.formData.cityCode) {
        this.$Message.warning('未选择城市')
        return
      }
      query = query.trim()
      if (!query || query === this.oldSearchName) {
        return
      }
      this.loading1 = true
      this.teacherNameList = []
      let req = {
        sysName: query,
        teacherType: 0,
        cityCode: this.formData.cityCode,
        businessType: this.formData.teacherType,
        pageNo: 1,
        pageSize: 20
      }
      this.getTeacherByNameFuzzyHandler(req).then(res => {
        this.teacherNameList = res.records
        this.loading1 = false
      }).catch(err => {
        this.loading1 = false
        this.$Message.error('获取主讲老师姓名失败:' + err.message)
      })
    },
    optionChange (val) {
      if (val && val.label) {
        this.oldSearchName = val.label
      }
    },
    clearTeacher () {
      this.oldSearchName = ''
      if (this.formData.teacherId) {
        this.formData.teacherId = ''
      }
    },
    changeArea (val, serviceCenterId) { // 改变区域
      this.serviceCenterList = []
      this.formData.serviceCenterId = ''
      if (!val) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        areaId: this.formData.areaId
      }
      this.getServiceCenterListHandler(req).then(res => {
        this.serviceCenterList = res
        this.serviceCenterList.forEach((value) => {
          if (!value.address) {
            value.address = ''
          }
        })
        if (serviceCenterId) {
          this.formData.serviceCenterId = serviceCenterId
          this.changeService(serviceCenterId)
        }
      }).catch(err => {
        this.$Message.warning('获取服务中心失败:' + err.message)
      })
    },
    changeService (val) { // 改变服务中心
      this.venueList = []
      this.formData.venueId = ''
      if (!val) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        serviceId: this.formData.serviceCenterId
      }
      this.getVenueListHandler(req).then(res => {
        this.venueList = res
      }).catch(err => {
        this.$Message.warning('获取教学点失败:' + err.message)
      })
    },
    // 查询
    search (data) {
      this.getTableData(data)
    },
    // 重置
    resetField () {
      this.formData = Object.assign({}, defaultSetQuery)
      if (this.cityWithoutAllCity) {
        this.formData.cityCode = this.cityWithoutAllCity[0].id
        this.cityYearChange(this.formData.cityCode)
      }
      this.$refs['form'].resetFields()
    },
    // 分页
    changePage (num) {
      this.currentPageNum = num
      this.getTableData('form')
    },
    pageSizeChange (size) {
      this.currentPageNum = 1
      this.currentPageSize = size
      this.getTableData('form')
    },
    // 表格数据
    getTableData (data) {
      this.$refs[data].validate((valid) => {
        if (valid) {
          this.formData.pageSize = this.currentPageSize
          this.formData.pageNo = this.currentPageNum
          this.tableLoading = true
          queryWaitList(this.formData).then(res => {
            if (res.records.length === 0) {
              this.tableData = []
              this.$Message.warning('暂无数据')
            }
            this.pageTotal = res.totalCount
            this.tableData = res.records
            this.tableLoading = false
          }).catch(err => {
            this.tableLoading = false
            this.pageTotal = 0
            this.tableData = []
            this.currentPageNum = 1
            this.currentPageSize = 10
            this.$Message.warning({ content: '获取数据失败:' + err.message, duration: 5 })
          })
        }
      })
    },
    startCreateDateChange (val) {
      if (this.formData.endCreateDate) {
        if (new Date(val).getTime() > new Date(this.formData.endCreateDate).getTime()) {
          this.$Message.warning({ content: '结束时间不得小于开始时间', duration: 5 })
          this.formData.startCreateDate = ''
          this.formData.startCreateDateVal = ''
          return
        }
      }
      this.formData.startCreateDate = val
    },
    endCreateDateChange (val) {
      if (this.formData.startCreateDate) {
        if (new Date(this.formData.startCreateDate).getTime() > new Date(val).getTime()) {
          this.$Message.warning({ content: '结束时间不得小于开始时间', duration: 5 })
          this.formData.endCreateDate = ''
          this.formData.endCreateDateVal = ''
          return
        }
      }
      this.formData.endCreateDate = val
    },
    applyClass (params) {
      this.row = params.row
      this.studentDetail = {
        cityCode: this.formData.cityCode,
        studentId: params.row.studentId
      }
      this.applyShow = true
    },
    // 转班
    reverseClass (params) {
      this.row = params.row
      this.studentDetail = {
        cityCode: this.formData.cityCode,
        studentId: params.row.studentId,
        uid: params.row.studentUid
      }
      querySourceClass({
        cityCode: this.formData.cityCode,
        studentId: params.row.studentId,
        type: 1,
        year: this.formData.year,
        termId: this.formData.termId,
        subjectId: params.row.subjectLongValue
      }).then(res => {
        if (res.length <= 0) {
          this.$Message.warning('暂无原班，无法转入')
        } else {
          this.reverseData = res
          this.reverseShow = true
          getChangeClassType({
            cityCode: this.formData.cityCode
          }).then(res => {
            this.reverseTypeList = res
          }).catch(error => {
            this.$Message.error({ content: '获取转班类型失败：' + error.message })
          })
        }
      }).catch(err => {
        this.$Message.error({ content: '获取原班失败:' + err.message })
      })
    },
    removeWaitRecords (params) {
      this.$Modal.confirm({
        title: '删除',
        content: '<p>确认删除吗?</p>',
        loading: true,
        onOk: () => {
          let deleteData = {
            cityCode: this.formData.cityCode,
            studentId: params.row.studentId,
            classId: params.row.classId,
            modifier: this.empNo
          }
          deleteAlternateRecord(deleteData).then(res => {
            this.$Message.success('删除成功')
            this.$Modal.remove()
            this.tableLoading = true
            setTimeout(() => {
              this.getTableData('form')
            }, 2000)
          }).catch(err => {
            this.$Message.error('删除失败:' + err.message)
            this.$Modal.remove()
          })
        }
      })
    },
    selectionChange (selection) {
      this.selectedRows = selection
    },
    batchRemove () {
      this.$Modal.confirm({
        title: '删除',
        content: '<p>确认批量删除吗?</p>',
        loading: true,
        onOk: () => {
          batchRemoveWait({
            cityCode: this.formData.cityCode,
            waitIds: this.selectedRows.map(s => s.id)
          }).then(res => {
            this.$Message.success('批量删除成功')
            this.$Modal.remove()
            this.tableLoading = true
            setTimeout(() => {
              this.getTableData('form')
            }, 2000)
          }).catch(err => {
            this.$Message.error('批量删除失败:' + err.message)
            this.$Modal.remove()
          })
        }
      })
    },
    exportExcel () {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.exportLoading = true
          exportWaitListExportExcel(this.formData).then(res => {
            const reader = new FileReader()
            reader.addEventListener('loadend', (e) => {
              try {
                const result = JSON.parse(e.target.result)
                this.$Message.error((result && result.message) || '导出失败！')
              } catch (errMsg) {
                let objectUrl = URL.createObjectURL(new Blob([res]))
                if (objectUrl) {
                  const link = document.createElement('a')
                  link.download = '候补批处理' + new Date().getTime() + '.xlsx'
                  link.href = objectUrl
                  link.click()
                }
              }
            })
            reader.readAsText(new Blob([res]))
          }).catch(err => {
            this.$Message.error({ content: '获取数据失败:' + err.message || err, duration: 5 })
          }).finally(() => {
            this.exportLoading = false
          })
        }
      })
    }
  }
}
</script>
