  #TalClassAttendance {
    width: 100%;
    overflow-x: hidden;
    padding: 10px;
  }
  .container {
    width: 100%;
    padding-left: 20px;
    padding-right: 24px;
  }

  .class_top {
    background: #fff;
    padding-top: 24px;
    font-size: 14px;
  }
</style>
